import common from '../../../common/common.js'

export default {
	data() {
		return {
			userid:'',
			userInfo:[],
			info:{},
			
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
		};
	},
	onLoad() {
		var that = this
		var userInfo = uni.getStorageSync('userInfo');
		that.userInfo = userInfo
		var userid = uni.getStorageSync('userid');
		that.userid = userid
		
		var currentPages = getCurrentPages()
		that.info.delta = currentPages.length
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
	},
	methods:{
		//提交
		formSubmit(e){
			var that = this
			var data = e.detail.value;
			var nickname = data.nickname
			
			
			
			if(nickname == '' || nickname.length < 3){					
				uni.showToast({
					title: '请输入昵称',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			uni.showLoading({
				title: '处理中'
			});
			
			uni.request({
				url: common.apiUrl + 'user/updateNickname',
				method: 'GET',
				data: {
					userid:that.userid,
					nickname:nickname
				},
				success: res => {
					uni.hideLoading();
					
					var userInfo = uni.getStorageSync('userInfo');
					userInfo.users_nickname = nickname
					uni.setStorageSync('userInfo',userInfo);
					that.userInfo = userInfo
					
					uni.showToast({
						title: '操作成功',
						duration: 1500,
						icon:'success'
					});
					uni.navigateBack();
				},
				fail: () => {},
				complete: () => {}
			});
			
			
			
		}
	}
}
